import React, { useContext, useEffect, useState } from "react";
import moment from "moment";
import { arrayUnion, doc, onSnapshot, updateDoc } from "firebase/firestore";
import { AuthContext } from "../context/AuthContext";
import { auth, db } from "../firebase";
import { uuidv4 as uuid } from "@firebase/util";
import { signOut } from "firebase/auth";

const Inputs = () => {
  const { currentUser: c } = useContext(AuthContext);
  const [inputs, setInputs] = useState({
    email: "",
    name: "",
    desc: "",
  });
  const [leads, setLeads] = useState([]);

  const [open, setOpen] = useState(false);
  const handleInputOpen = (e) => {
    setOpen(true);
  };
  const handleInputClose = (e) => {
    e.preventDefault();
    inputs.email = "";
    inputs.name = "";
    inputs.desc = "";
    setOpen(false);
  };

  useEffect(() => {
    if (c.uid) {
      const unsub = onSnapshot(doc(db, "leads", c.uid), (doc) => {
        doc.exists() && setLeads(doc.data().leads);
      });

      return () => {
        unsub();
      };
    }
  }, [c]);

  const object = [];
  const handleChange = (e) => {
    setInputs((prev) => ({ ...prev, [e.target.name]: e.target.value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const email = [];
    inputs.email.split(" ").forEach((e) => {
      e.includes("@") &&
        e.includes(".com") &&
        email.push(
          e
            .replace(",", "")
            .replace(". ", "")
            .replace(";", "")
            .replace(".com.", ".com")
        );
    });
    email.forEach((em) =>
      object.push({
        id: uuid(),
        email: em,
        name: inputs.name,
        desc: inputs.desc,
        date: moment().format("MMMM Do YYYY, h:mm:ss a"),
        time: new Date().getTime(),
      })
    );
    object.forEach(async (o) => {
      try {
        let notExist = true;
        if (leads.length !== 0) {
          leads.forEach((ld) => {
            if (ld.email === o.email) notExist = false;
          });
        }

        if (notExist) {
          //create a chat in chats collection
          await updateDoc(doc(db, "leads", c.uid), {
            leads: arrayUnion(o),
          });
        }
      } catch (err) {
        console.log(err);
      }
    });
    setInputs({
      email: "",
      name: "",
      desc: "",
    });
    setOpen(false);
  };

  const signout = async (e) => {
    e.preventDefault();
    try {
      await signOut(auth);
    } catch (err) {
      console.log(err);
    }
  };

  return (
    <>
      {!open && (
        <div className="inputButtons">
          <div className="addButton">
            <img
              onClick={handleInputOpen}
              src="https://i.ibb.co/Yy10RDY/plus.png"
              alt="Add"
            />
          </div>
          <div className="signoutBtn">
            <img
              onClick={signout}
              src="https://i.ibb.co/NrVT14J/exit.png"
              alt="exit"
              border="0"
            />
          </div>
        </div>
      )}
      {open && (
        <div className="inputs">
          <div className="firstRow">
            <input
              type="text"
              placeholder="Email"
              name="email"
              onChange={handleChange}
            />
            <input
              type="text"
              placeholder="Name"
              name="name"
              onChange={handleChange}
            />
          </div>
          <textarea
            placeholder="Description"
            name="desc"
            onChange={handleChange}
          ></textarea>
          <div className="buttons">
            <button onClick={handleInputClose}>Cancel</button>
            <button onClick={handleSubmit}>Submit</button>
          </div>
        </div>
      )}
    </>
  );
};

export default Inputs;
