import {
  doc,
  FieldValue,
  Firestore,
  onSnapshot,
  updateDoc,
} from "firebase/firestore";
import React, { useContext, useEffect, useState } from "react";
import { AuthContext } from "../context/AuthContext";
import { db } from "../firebase";
import moment from "moment";

const LeadCollections = () => {
  const { currentUser: c } = useContext(AuthContext);
  const [leads, setLeads] = useState([]);
  const [cInputs, setCInputs] = useState([]);
  const [recieverMail, setRecieverMail] = useState("");

  const handleHide = (e) => {
    e.target.parentElement.classList.add("hideDesc");
    e.target.parentElement.parentElement
      .querySelector(".firstRow")
      .querySelector(".arrowDown")
      .classList.remove("hide");
    e.target.parentElement.parentElement.style.marginBottom = 0;
  };

  const handleShow = (e) => {
    e.preventDefault();
    e.target.classList.add("hide");
    document.querySelectorAll(".leads .item .secondRow").forEach((lis) => {
      if (
        lis !== e.target.parentElement.parentElement.querySelector(".secondRow")
      ) {
        lis.classList.add("hideDesc");
        lis.parentElement
          .querySelector(".firstRow")
          .querySelector(".arrowDown")
          .classList.remove("hide");
      }
    });
    e.target.parentElement.parentElement
      .querySelector(".secondRow")
      .classList.remove("hideDesc");
    e.target.parentElement.parentElement.style.marginBottom = "30px";
  };

  useEffect(() => {
    if (c.uid) {
      const unsub = onSnapshot(doc(db, "leads", c.uid), (doc) => {
        doc.exists() && setLeads(doc.data().leads);
      });

      return () => {
        unsub();
      };
    }
  }, [c.uid]);

  const handleCopy = (e) => {
    const txt = e.target.previousElementSibling.textContent;
    navigator.clipboard.writeText(txt);
  };

  const handleCheck = () => {
    const checkBox = document.querySelectorAll(".checkbox");
    let x = [];
    checkBox.forEach((ch) => {
      ch.checked &&
        x.push(ch.parentElement.parentElement.querySelector("p").textContent);
    });
    setCInputs(x);
  };

  const handleRemove = (e) => {
    e.preventDefault();
    const re = e.target.parentElement.querySelector("span").textContent;
    document.querySelectorAll(".checkbox").forEach((rb) => {
      if (rb.parentElement.querySelector("p").textContent === re)
        rb.checked && rb.click();
    });
  };

  useEffect(() => {
    let recieverMail = "";
    for (let i = 0; i < cInputs.length; i++) {
      if (i !== cInputs.length - 1) {
        recieverMail += cInputs[i] + ", ";
      } else {
        recieverMail += cInputs[i];
      }
    }
    setRecieverMail(recieverMail);
  }, [cInputs]);

  const selectAll = (e) => {
    e.preventDefault();
    document.querySelectorAll(".checkbox").forEach((cb) => {
      !cb.checked && cb.click();
    });
  };

  const handleCancel = (e) => {
    e.preventDefault();
    document.querySelectorAll(".checkbox").forEach((cb) => {
      cb.checked && cb.click();
    });
  };
  const handleDelete = async (e, ob) => {
    e.preventDefault();
    try {
      await updateDoc(doc(db, "leads", c.uid), {
        leads: leads.filter((li) => li !== ob),
      });
    } catch (err) {
      console.log(err);
    }
  };
  const multipleDelete = (e) => {
    e.preventDefault();
    try {
      cInputs.forEach(async (ci) => {
        await updateDoc(doc(db, "leads", c.uid), {
          leads: leads.filter((li) => !cInputs.includes(li.email)),
        });
      });
      setCInputs([]);
    } catch (err) {
      console.log(err);
    }
  };

  return (
    <div>
      <div className="selectedLeads">
        {cInputs.length !== 0 && (
          <>
            <div className="leadItems">
              {cInputs.map((ci) => (
                <div>
                  <span>{ci}</span>
                  <button onClick={handleRemove}></button>
                </div>
              ))}
            </div>
            <div className="buttons">
              <button onClick={selectAll}>Select All</button>
              <button onClick={handleCancel}>Cancel</button>
              <button onClick={multipleDelete}>Delete</button>
              <a href={`mailto: ${recieverMail}`}>Send</a>
            </div>
          </>
        )}
      </div>

      <div className="leads">
        {leads
          .sort((a, b) => b.time - a.time)
          .map((li) => (
            <div className="item">
              <div className="firstRow">
                {(li.name || li.desc) && (
                  <img
                    onClick={handleShow}
                    className="arrowDown"
                    src="https://i.ibb.co/7zdc21s/up-arrow-1.png"
                    alt="up-arrow-1"
                    border="0"
                  />
                )}

                <input
                  onChange={handleCheck}
                  className="checkbox"
                  type="checkbox"
                  checked={cInputs.includes(li.email)}
                />
                <p>{li.email}</p>
                <img
                  onClick={handleCopy}
                  className="copy"
                  src="https://i.ibb.co/23PmTh3/link.png"
                  alt="link"
                  border="0"
                ></img>
                <span className="date">{moment(li.time).fromNow()} </span>
                <img
                  onClick={(e) => handleDelete(e, li)}
                  className="delete"
                  src="https://i.ibb.co/rkmd1d2/trash.png"
                  alt="trash"
                  border="0"
                />
              </div>
              {(li.name || li.desc) && (
                <div className="secondRow hideDesc">
                  <img
                    className="arrowUp"
                    onClick={handleHide}
                    src="https://i.ibb.co/7zdc21s/up-arrow-1.png"
                    alt="up-arrow-1"
                    border="0"
                  />
                  {li.name && (
                    <div className="secondRowCol1">
                      <p className="name">{li.name}</p>
                      <img
                        onClick={handleCopy}
                        className="copy"
                        src="https://i.ibb.co/23PmTh3/link.png"
                        alt="link"
                        border="0"
                      ></img>
                    </div>
                  )}
                  {li.desc && <p className="desc">{li.desc}</p>}
                </div>
              )}
            </div>
          ))}
      </div>
    </div>
  );
};

export default LeadCollections;
